import icon_pools from '@/assets/images/icon_pools@2x.png'
import icon_positions from '@/assets/images/icon_positions@2x.png'
import pool_bg from '@/assets/images/pool_bg.png'
import usePoolsStore from '@/store/pool'
import usePositionStore from '@/store/position'
import useStatsStore from '@/store/stats'
import { SelectTab } from '@cetus/design'
import useQueryParams from '@cetus/hooks/src/useQueryParams'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { VTextLabelBox } from '@cetus/ui-kit'
import { Box, Button, HStack, Text, Tooltip, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import LpBurnModal from '../position/LpBurnModal'
import VolChartPageBlock from '../stats/VolChartPageBlock'

function PoolsTopData({ isRefreshed }: { isRefreshed: boolean }) {
  const { tab } = useQueryParams()
  const navigate = useNavigate()
  const [isOpenLpBurnModal, setIsOpenLpBurnModal] = useState<boolean>(false)
  const { statisticsData } = useStatsStore()
  const { poolListLength } = usePoolsStore()
  const { showPosListLength } = usePositionStore()
  const tabList = useMemo(() => {
    console.log('🚀 ~ PoolsTopData ~ poolListLength:', poolListLength)
    return [
      {
        label: 'Pools',
        value: 'pools',
        num: poolListLength,
        imgInfo: {
          src: icon_pools,
          w: '22px',
          h: '22px'
        }
      },
      {
        label: 'Positions',
        value: 'positions',
        num: showPosListLength,
        imgInfo: {
          src: icon_positions,
          w: '22px',
          h: '22px'
        }
      }
    ]
  }, [poolListLength, showPosListLength])

  const { isApp } = useWindowWidth()
  const [isTooltipOpen, setTooltipOpen] = useState(false)
  return (
    <VStack w="100%">
      <HStack
        w="100%"
        gap={{
          base: '12px',
          lg: '32px'
        }}
        flexDirection={{
          base: 'column',
          lg: 'row'
        }}
      >
        <VStack
          w={{
            base: '100%',
            lg: '395px'
          }}
          justify="space-between"
          align="flex-start"
          gap={{
            base: '12px',
            lg: '40px'
          }}
        >
          <Text fontSize="24px" fontWeight="500" color="text_caption">
            Liquidity Pools
          </Text>
          <VStack
            gap="68px"
            p="32px 40px"
            align={{
              base: 'center',
              lg: 'flex-start'
            }}
            borderRadius="16px"
            bgImage={pool_bg}
            bgPosition="center"
            w="100%"
            h="240px"
            backgroundSize="100% 100%"
          >
            <VTextLabelBox
              wrapStyle={{
                alignItems: isApp ? 'center' : 'flex-start',
                gap: '12px'
              }}
              titleStyle={{
                fontSize: '16px'
              }}
              valueStyle={{
                fontSize: '20px',
                fontWeight: '600'
              }}
              title="Total Value Locked"
              value={statisticsData?.totalValueLocked || ''}
              isLoading={!statisticsData}
            />
            <VTextLabelBox
              wrapStyle={{
                alignItems: isApp ? 'center' : 'flex-start',
                gap: '12px'
              }}
              titleStyle={{
                fontSize: '16px'
              }}
              valueStyle={{
                fontSize: '20px',
                fontWeight: '600'
              }}
              title="Cumulative Volume"
              value={statisticsData?.cumulativeVolume || ''}
              isLoading={!statisticsData}
            />
          </VStack>
        </VStack>
        <Box
          h="100%"
          mb="-4px"
          w={{
            base: '100%',
            lg: 'calc(100% - 395px - 32px)'
          }}
        >
          <VolChartPageBlock statisticsData={statisticsData} pageFrom="pools" isRefresh={!isRefreshed} />
        </Box>
      </HStack>
      <HStack
        w="100%"
        mt="32px"
        justify="space-between"
        flexDirection={{
          base: 'column-reverse',
          lg: 'row'
        }}
      >
        <SelectTab
          type="borderTab"
          wrapStyle={{
            w: {
              base: '100%',
              lg: '540px'
            },
            h: '60px'
          }}
          itemStyle={{
            w: '50%',
            fontSize: '16px'
          }}
          tabList={tabList}
          currentTab={(tabList?.find(item => item?.value === tab)?.label as string) || 'Pools'}
          handleChangeTab={(item: any) => {
            // if (item.value == 'positions') {
            //   navigate('/pools?tab=positions')
            // } else {
            //   navigate('/pools?tab=pools')
            // }
            navigate(`/pools?tab=${item.value}`)
          }}
        />
        <HStack w={{ base: '100%', lg: 'unset' }}>
          {tab == 'positions' ? (
            <Tooltip
              isOpen={isTooltipOpen}
              label={
                <Text fontSize="12px" lineHeight="20px">
                  Permanently burn/lock liquidity for an existing position. Trading fees and mining rewards earned remain claimable.
                </Text>
              }
              aria-label="Yield"
              placement="top"
            >
              <Button
                w={{ base: '50%', lg: '168px' }}
                fontWeight="500"
                variant="outline"
                as="div"
                borderRadius="8px"
                onMouseEnter={() => setTooltipOpen(true)}
                onMouseLeave={() => setTooltipOpen(false)}
                onClick={() => {
                  setTooltipOpen(false)
                  setIsOpenLpBurnModal(true)
                }}
              >
                LP Burn
              </Button>
            </Tooltip>
          ) : (
            <Button borderRadius="8px" w={{ base: '50%', lg: '168px' }} fontWeight="500" variant="outline" onClick={() => navigate('/create-pool')}>
              Create a new pool
            </Button>
          )}
          <Button borderRadius="8px" fontWeight="500" w={{ base: '50%', lg: '168px' }} onClick={() => navigate('/select-pool')}>
            Add Liquidity
          </Button>
        </HStack>
      </HStack>
      {isOpenLpBurnModal && <LpBurnModal isOpen={isOpenLpBurnModal} onClose={() => setIsOpenLpBurnModal(false)} />}
    </VStack>
  )
}

export default PoolsTopData
