import useNavigateToLiquidity from '@/hooks/liquidity/useNavigateToLiquidity'
import { CetusTooltip } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { Button, Center, HStack, Popover, PopoverBody, PopoverContent, PopoverTrigger, Portal, Text } from '@chakra-ui/react'
import { useState } from 'react'
import VaultModal from '../vaults/modal/index'
import ActionContent from './ActionContent'

function ActionsBlock({ poolInfo }: { poolInfo: any }) {
  const { isApp } = useWindowWidth()

  const { goLiquidity } = useNavigateToLiquidity()

  const [isOpenVaultUnstableModal, setIsOpenVaultUnstableModal] = useState(false)
  const [isShowAutoTip, setIsShowAutoTip] = useState(false)

  // useEffect(() => {
  //   if (!isOpenVaultUnstableModal) {
  //     setIsShowAutoTip(true)
  //   }
  // }, [isOpenVaultUnstableModal])
  return (
    <HStack
      w={{
        base: 'calc(100vw - 8px)',
        lg: 'unset'
      }}
      pr={{
        base: '16px',
        lg: 'unset'
      }}
      justify={isApp ? 'space-between' : 'flex-end'}
      gap="8px"
    >
      {poolInfo.isVaults || poolInfo.isUnstableVault ? (
        <CetusTooltip
          placement="bottom-end"
          tooltip={
            <Text _hover={{ color: 'primary' }} cursor="pointer" color={isApp ? 'primary' : 'text_paragraph'}>
              Auto
            </Text>
          }
          showTooltip={isShowAutoTip}
        >
          <Center w={isApp ? 'calc(100%)' : 'unset'}>
            <Button
              p="0"
              variant="ghost"
              w={{
                base: '100%',
                lg: '32px'
              }}
              padding={isApp ? '0 12px' : ''}
              h="32px"
              borderRadius="8px"
              sx={{
                _hover: {
                  svg: {
                    fill: 'primary'
                  }
                }
              }}
              onClick={(e: any) => {
                e.stopPropagation()
                setIsOpenVaultUnstableModal(!isOpenVaultUnstableModal)
              }}
              onMouseMove={() => {
                setIsShowAutoTip(true)
              }}
              onMouseLeave={() => {
                setIsShowAutoTip(false)
              }}
            >
              <Icon svgFill={isApp ? 'primary' : 'text_paragraph'} xlinkHref="#icon-icon_auto" svgW="20px" svgH="20px" />
              {isApp && (
                <Text color={isApp ? 'primary' : 'text_paragraph'} marginLeft="4px">
                  Auto
                </Text>
              )}
            </Button>
          </Center>
        </CetusTooltip>
      ) : null}
      <Button
        w={{ base: 'calc(100% - 32px)', lg: 'unset' }}
        p="8px"
        h="32px"
        fontSize={{ base: '14px', lg: '12px' }}
        borderRadius="8px"
        fontWeight="500"
        onClick={() => {
          // navigate(`/liquidity?poolAddress=${poolInfo?.poolAddress}`)
          goLiquidity(`/liquidity?poolAddress=${poolInfo?.poolAddress}`, poolInfo)
        }}
      >
        Deposit
      </Button>
      <Popover isLazy trigger={isApp ? 'click' : 'hover'} placement={isApp ? 'top-start' : 'bottom-end'} autoFocus={false} returnFocusOnClose={false}>
        <PopoverTrigger>
          <Center
            onClick={(e: any) => {
              cancelBubble(e)
            }}
          >
            <Button
              variant="ghost"
              w="32px"
              h="32px"
              p="0"
              borderRadius="8px"
              sx={{
                _hover: {
                  svg: {
                    fill: 'primary'
                  }
                }
              }}
            >
              <Icon svgFill={isApp ? 'primary' : 'text_paragraph'} xlinkHref="#icon-icon_more" svgW="16px" svgH="16px" />
            </Button>
          </Center>
        </PopoverTrigger>
        <Portal>
          <PopoverContent borderRadius="12px" w="fit-content">
            <PopoverBody p="0" fontSize="12px" w="fit-content">
              <ActionContent poolInfo={poolInfo} />
            </PopoverBody>
          </PopoverContent>
        </Portal>
      </Popover>
      {isOpenVaultUnstableModal ? (
        <VaultModal
          isOpen={isOpenVaultUnstableModal}
          setIsOpen={setIsOpenVaultUnstableModal}
          displayTokenA={poolInfo.displayTokenA}
          displayTokenB={poolInfo.displayTokenB}
          feeDisplay={poolInfo.feeDisplay}
          clmmPool={poolInfo.poolAddress}
          isUnstableVault={poolInfo.isUnstableVault}
          vaultId={poolInfo.vaultId}
          isReverse={poolInfo.isReverse}
        />
      ) : null}
    </HStack>
  )
}

export default ActionsBlock
