import useNavigateToLiquidity from '@/hooks/liquidity/useNavigateToLiquidity'
import useFavoritePool from '@/hooks/pool/useFavoritePool'
import usePoolsStore from '@/store/pool'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import { useNavigate } from 'react-router-dom'

export default function ActionContent({ poolInfo }: { poolInfo: any }) {
  const navigate = useNavigate()
  const { addFavorites, removeFavorites } = useFavoritePool()
  const { poolFavoriteIds } = usePoolsStore()
  const { goLiquidity } = useNavigateToLiquidity()

  const isFavoritesPool: boolean = useMemo(() => {
    return poolFavoriteIds?.includes(poolInfo?.poolAddress || '')
  }, [JSON.stringify(poolFavoriteIds), poolInfo?.poolAddress])
  return (
    <VStack align="flex-start" padding="8px">
      <IconText
        text="Analytics"
        xlinkHref="#icon-icon_kline"
        onClick={() => {
          goLiquidity(`/liquidity?tab=analytics&poolAddress=${poolInfo?.poolAddress}`, poolInfo)
          // navigate(`/liquidity?tab=analytics&poolAddress=${poolInfo?.poolAddress}`)
        }}
      />
      <IconText
        text="Swap"
        xlinkHref="#icon-icon_swap1"
        onClick={() => {
          navigate(`/swap/${poolInfo?.displayTokenA?.coin_type}/${poolInfo?.displayTokenB?.coin_type}`)
        }}
      />
      <IconText
        text="Add to Watchlist"
        xlinkHref={isFavoritesPool ? '#icon-icon_star_sel' : '#icon-icon_star'}
        svgFill={isFavoritesPool ? 'primary' : 'text_paragraph'}
        onClick={() => {
          if (isFavoritesPool) {
            removeFavorites(poolInfo?.poolAddress)
          } else {
            addFavorites(poolInfo?.poolAddress)
          }
        }}
      />
    </VStack>
  )
}

const IconText = ({
  text,
  xlinkHref,
  svgFill = 'text_paragraph',
  onClick
}: {
  text: string
  xlinkHref: string
  onClick: () => void
  svgFill?: string
}) => {
  return (
    <HStack
      w="100%"
      cursor="pointer"
      bg="menu_item_bg"
      borderRadius="8px"
      padding="11px"
      _hover={{
        svg: {
          fill: 'primary'
        },
        p: {
          color: 'primary'
        }
      }}
      onClick={(e: any) => {
        cancelBubble(e)
        onClick()
      }}
    >
      <Icon
        svgFill={svgFill}
        xlinkHref={xlinkHref}
        svgHover="primary"
        svgW={text == 'Add to Watchlist' ? '22px' : '20px'}
        svgH={text == 'Add to Watchlist' ? '22px' : '20px'}
        minW={text == 'Add to Watchlist' ? '22px' : '20px'}
        minH={text == 'Add to Watchlist' ? '22px' : '20px'}
        ml={text == 'Add to Watchlist' ? '-2px' : '0px'}
      />
      <Text fontSize="13px">{text}</Text>
    </HStack>
  )
}
