import useNotifiSubscription from '@/hooks/notifi/useNotifiSubscription'
import usePositionList from '@/hooks/position/usePositionList'
import useStatistics from '@/hooks/stats/useStatistics'
import usePositionStore from '@/store/position'
import { useRpcListener } from '@cetus/hooks'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { useAccountStore } from '@cetus/stores'
import useNotifiStore from '@cetus/stores/src/notifi'
import { Token } from '@cetus/types'
import { NoData } from '@cetus/ui-kit'
import { d } from '@cetus/utils'
import { fixCoinType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { Box, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import PoolsFilter from '../../pools/PoolsFilter'
import LiquidityAndYield from './LiquidityAndYield'
import PoolItem from './PoolItem'
import PositionListLoading from './PositionListLoading'

function PositionsContent({ isRefreshed, handleIsRefreshed }: { isRefreshed: boolean; handleIsRefreshed: (isFreshed: boolean) => void }) {
  const { getStatistics } = useStatistics()
  const { fetchTokenPrices } = useTokenPrice()
  const { currentAccount, onWalletModal } = useAccountStore()
  const { getPositionBaseList, getPosRelatedData } = usePositionList()
  const { posRewardsData, posBaseList, setShowPosListLength, setMyPosYieldValue, posBaseListLoading, posBaseListGroupByPool } = usePositionStore()
  // const [showPosList, setShowPosList] = useState<any>([])
  const [showPosListGroupByPool, setShowPosListGroupByPool] = useState<any>([])
  const { getNotifiPositionTransfer } = useNotifiSubscription()
  const handleRefresh = () => {
    console.log('🚀 ~ handleRefresh ~ handleRefresh:')
    handleIsRefreshed(false)
    setMyPosYieldValue('')
    getStatistics()
    refreshTokenPrice()
    if (currentAccount?.address) {
      handleGetPositionList(currentAccount?.address)
    }
    setTimeout(() => {
      handleIsRefreshed(true)
    }, 1000)
  }
  useRpcListener({
    onRpcChange: () => {
      if (currentAccount?.address) {
        handleGetPositionList(currentAccount?.address)
      }
    }
  })
  const [selectCoinList, setSelectCoinList] = useState<Token[]>([])
  const onClickSelectCoinList = (tokenInfo: Token) => {
    console.log('🚀 ~ onClickIncentiveTypes ~ tokenInfo:', tokenInfo)
    setSelectCoinList(prev => [...prev, tokenInfo])
  }
  const onDeleteSelectCoinList = (tokenInfo: Token) => {
    console.log('🚀 ~ onClickIncentiveTypes ~ tokenInfo:', tokenInfo)
    setSelectCoinList(prev => prev.filter(ele => ele?.coin_type !== tokenInfo?.coin_type))
  }
  useEffect(() => {
    setMyPosYieldValue('')
    if (currentAccount?.address) {
      handleGetPositionList(currentAccount?.address)
    } else {
      setShowPosListLength('')
    }
  }, [currentAccount?.address])

  const handleGetPositionList = async (walletAddress: string) => {
    await getPositionBaseList(walletAddress)
  }

  useEffect(() => {
    console.log('🚀 ~ useEffect ~ posBaseList:', posBaseList)
    getPosRelatedData(posBaseList)
  }, [JSON.stringify(posBaseList)])

  const { notifiClient, notifiSources } = useNotifiStore()
  useEffect(() => {
    getNotifiPositionTransfer(posBaseList, notifiClient, notifiSources)
  }, [posBaseList, notifiClient, notifiSources])

  useEffect(() => {
    const firstTokenAddress = selectCoinList[0]?.coin_type ? fixCoinType(selectCoinList[0]?.coin_type) : undefined
    const lastTokenAddress = selectCoinList[1]?.coin_type ? fixCoinType(selectCoinList[1]?.coin_type) : undefined
    const filterArr = Object.values(posBaseListGroupByPool).filter((filterItem: any) => {
      if (firstTokenAddress && lastTokenAddress) {
        return (
          (fixCoinType(filterItem.tokenA.coin_type) == firstTokenAddress && fixCoinType(filterItem.tokenB.coin_type) == lastTokenAddress) ||
          (fixCoinType(filterItem.tokenA.coin_type) == lastTokenAddress && fixCoinType(filterItem.tokenB.coin_type) == firstTokenAddress)
        )
      } else if (firstTokenAddress) {
        return fixCoinType(filterItem.tokenA.coin_type) == firstTokenAddress || fixCoinType(filterItem.tokenB.coin_type) == firstTokenAddress
      } else {
        return filterItem
      }
    })
    console.log('🚀 ~ useEffect ~ filterArr:', Object.values(posBaseListGroupByPool), filterArr)
    setShowPosListGroupByPool(filterArr)
    const posListLength = filterArr.reduce((sum, item: any) => {
      return sum + (item.list ? item.list.length : 0)
    }, 0)
    if (currentAccount?.address) {
      setShowPosListLength(posListLength.toString())
    }
  }, [selectCoinList, posBaseListGroupByPool])

  // 刷新token价格
  const refreshTokenPrice = () => {
    const list: any = []
    if (posBaseList?.length > 0) {
      posBaseList.map(item => {
        list.push(item?.coinTypeA)
        list.push(item?.coinTypeB)
        const currentPosData = posRewardsData[item?.posId]
        currentPosData?.map((reward: any) => {
          if (d(reward?.display_amount_owed).gt(0)) {
            list.push(reward?.coin_address)
          }
        })
      })
      const tokenArr: any = Array.from(new Set(list))
      console.log('🚀 ~ refreshTokenPrice ~ list:', posBaseList, list, tokenArr)
      fetchTokenPrices(tokenArr)
    }
  }

  useEffect(() => {
    refreshTokenPrice()
  }, [posBaseList?.length, posRewardsData])

  useEffect(() => {
    getStatistics()
    // 组件卸载时 列表条数重置
    return () => {
      setShowPosListLength('')
    }
  }, [])
  useEffect(() => {
    if (posBaseListLoading) {
      setShowPosListLength('')
    }
  }, [posBaseListLoading])

  return (
    <VStack gap="20px" w="100%">
      <PoolsFilter
        isPools={false}
        handleRefresh={handleRefresh}
        selectCoinList={selectCoinList}
        onClickSelectCoinList={onClickSelectCoinList}
        onDeleteSelectCoinList={onDeleteSelectCoinList}
      />
      <Box h="1px" w="100%" bg="border" />
      <LiquidityAndYield />
      {!currentAccount?.address ? (
        <NoData type="nowallet" onboard={() => onWalletModal(true)} />
      ) : posBaseListLoading ? (
        [{}, {}, {}, {}].map((item, index) => {
          return <PositionListLoading key={index} />
        })
      ) : Object.values(showPosListGroupByPool)?.length > 0 ? (
        Object.values(showPosListGroupByPool).map((item, index) => {
          return <PoolItem key={index} poolInfo={item} />
        })
      ) : (
        <NoData type="nodata" text="No liquidity positions" />
      )}
    </VStack>
  )
}

export default PositionsContent
