import PoolsContent from '@/components/pools/PoolsContent'
import PoolsTopData from '@/components/pools/PoolsTopData'
import PositionsContent from '@/components/position/list/PositionsContent'
import useQueryParams from '@cetus/hooks/src/useQueryParams'
import { VStack } from '@chakra-ui/react'
import { useState } from 'react'

function Pools() {
  const { tab } = useQueryParams()
  const [isRefreshed, setIsRefreshed] = useState(true)
  const handleIsRefreshed = (isRefreshed: boolean) => {
    setIsRefreshed(isRefreshed)
  }
  return (
    <VStack gap="20px" w="100%" pt="40px">
      <PoolsTopData isRefreshed={isRefreshed} />
      {(tab == 'pools' || tab === undefined) && <PoolsContent isRefreshed={isRefreshed} handleIsRefreshed={handleIsRefreshed} />}
      {tab == 'positions' && <PositionsContent isRefreshed={isRefreshed} handleIsRefreshed={handleIsRefreshed} />}
    </VStack>
  )
}

export default Pools
