import { CetusTooltip } from '@cetus/design'
import { useGetToken } from '@cetus/hooks/src/useToken'
import { SingleCoinImage } from '@cetus/ui-kit'
import { fixRounding, formatNumber, fromDecimalsAmountFix } from '@cetus/utils'
import { Box, Center, HStack, Text } from '@chakra-ui/react'
import { useEffect, useState } from 'react'

function RewardsBlock({ miningRewardList, farmsRewarderList }: { miningRewardList: any; farmsRewarderList: any }) {
  const { getTokenInfo } = useGetToken()
  const [rewardsList, setRewardsList] = useState<any>([])

  useEffect(() => {
    const fetchData = async () => {
      if (miningRewardList?.length > 0 || farmsRewarderList?.length > 0) {
        try {
          let miningList: any = []
          let farmingList: any = []
          if (miningRewardList?.length > 0) {
            miningList = await Promise.all(
              miningRewardList.map(async (item: any) => {
                const tokenInfo = await getTokenInfo(item?.coinType)
                return { ...item, tokenInfo, isMining: true }
              })
            )
          }
          if (farmsRewarderList?.length > 0) {
            farmingList = await Promise.all(
              farmsRewarderList.map(async (item: any) => {
                const tokenInfo = await getTokenInfo(item?.coinType)
                return { ...item, tokenInfo, isFarming: true }
              })
            )
          }
          setRewardsList(miningList.concat(farmingList))
        } catch (error) {
          console.error('Error processing list:', error)
        }
      }
    }

    fetchData()
  }, [miningRewardList, farmsRewarderList])

  return (
    <HStack w="100%" justify="flex-end">
      {rewardsList.map((item: any) => (
        <Box key={item?.coinType} onClick={(e: any) => e.stopPropagation()}>
          <CetusTooltip
            placement="top"
            tooltip={
              // <VStack align="flex-start">
              //   {rewardsList.map((item: any) => (
              <HStack key={item?.coinType} gap="4px">
                <Box>
                  <SingleCoinImage imageUrl={item?.tokenInfo?.logo_url} w="24px" h="24px" />
                </Box>
                {item?.isMining && (
                  <Text color="primary" display="inline-block">
                    {/* 处理精度后向上取整展示位隔符 */}
                    {formatNumber(fixRounding(fromDecimalsAmountFix(item?.emissionsEveryDay, item?.tokenInfo?.decimals), 2))}{' '}
                    {item?.tokenInfo?.symbol} per day in Mining
                  </Text>
                )}
                {item?.isFarming && (
                  <Text color="primary" display="inline-block">
                    {/* 处理精度后向上取整展示位隔符 */}
                    {formatNumber(fixRounding(item?.emissionsEveryDay, 2))} {item?.tokenInfo?.symbol} per day in Farming
                  </Text>
                )}
              </HStack>
              //   ))}
              // </VStack>
            }
          >
            <Center as="button">
              <SingleCoinImage imageUrl={item?.tokenInfo?.logo_url} w="24px" h="24px" />
            </Center>
          </CetusTooltip>
        </Box>
      ))}
    </HStack>
  )
}
export default RewardsBlock
