import tvl_bg from '@/assets/images/tvl_bg.png'
import yield_bg from '@/assets/images/yield_bg.png'
import useCalculatePendingYield from '@/hooks/position/useCalculatePendingYield'
import usePositionStore from '@/store/position'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import useTokenPriceStore from '@cetus/stores/src/tokenPrice'
import { Icon } from '@cetus/ui-kit'
import { d, formatCurrency } from '@cetus/utils'
import { Button, HStack, Skeleton, Stack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import PendingYieldModal from './PendingYieldModal'
import PendingYieldValue from './PendingYieldValue'

function LiquidityAndYield() {
  const { currentAccount } = useAccountStore()
  const [claimLoading, setClaimLoading] = useState(false)
  const [isOpenModal, setIsOpenModal] = useState(false)
  const [yieldList, setYieldList] = useState<any>([])
  const { getTokenAmountValue } = useTokenPrice()
  const { coinPriceObj } = useTokenPriceStore()
  const {
    myPosYieldValue,
    setMyPosYieldValue,
    posFeeDataLoading,
    posRewardsDataLoading,
    posBaseList,
    posLiquidityData,
    posBaseListLoading,
    posLiquidityDataLoading,
    posFeeData,
    posRewardsData
  } = usePositionStore()

  const totalTvl = useMemo(() => {
    let total: any = 0
    let totalAmount: any = 0
    if (posBaseList?.length > 0 && currentAccount?.address) {
      posBaseList.forEach((ele: any) => {
        const currentPosData = posLiquidityData[ele?.posId]
        console.log('🚀 ~ posBaseList.forEach ~ currentPosData:', currentPosData)
        const amountValueA = getTokenAmountValue(ele?.displayTokenA?.coin_type, currentPosData?.displayCoinAmountA)
        const amountValueB = getTokenAmountValue(ele?.displayTokenB?.coin_type, currentPosData?.displayCoinAmountB)
        // total == '--' || amountValueA == '--' || amountValueB == '--' ? '--' :
        total = d(total).plus(amountValueA).plus(amountValueB).toString()
        totalAmount = d(totalAmount)
          .plus(currentPosData?.displayCoinAmountA || 0)
          .plus(currentPosData?.displayCoinAmountB || 0)
          .toString()
      })
      return d(total).lte(0) && d(totalAmount).gt(0) ? '$--' : formatCurrency(total, 2)
    }
    return '$0'
  }, [posBaseList, posLiquidityData, currentAccount, coinPriceObj])

  const showLoading = !currentAccount?.address ? false : posBaseListLoading
  const { calculatePendingYield } = useCalculatePendingYield()

  useEffect(() => {
    if (!currentAccount?.address) {
      setMyPosYieldValue('')
      setYieldList([])
    } else {
      if (!posBaseListLoading && posBaseList?.length <= 0) {
        setMyPosYieldValue('0')
        setYieldList([])
      }
      if (posBaseList?.length > 0 && currentAccount?.address && !posBaseListLoading && !posFeeDataLoading && !posRewardsDataLoading) {
        const { total, rewardAndFeeList } = calculatePendingYield(posBaseList, posFeeData, posRewardsData)
        setMyPosYieldValue(total)
        setYieldList(rewardAndFeeList)
      }
    }
  }, [posBaseList, posBaseListLoading, posFeeDataLoading, posRewardsDataLoading, currentAccount, coinPriceObj])

  const showYieldLoading = !currentAccount?.address ? false : !myPosYieldValue
  const { isApp } = useWindowWidth()
  return (
    <Stack flexDir={{ base: 'column', lg: 'row' }} w="100%" gap="20px">
      <HStack
        justifyContent="space-between"
        w={{ base: '100%', lg: '50%' }}
        p="0 20px"
        h="80px"
        borderRadius="14px"
        bgColor="card_bg"
        bgImage={tvl_bg}
        backgroundSize="100% 100%"
      >
        <Text fontSize="16px" color="primary_gray">
          Total Liquidity
        </Text>
        <Skeleton isLoaded={!showLoading}>
          <Text fontSize="20px" color="primary">
            {totalTvl}
          </Text>
        </Skeleton>
      </HStack>
      <HStack
        justifyContent="space-between"
        p="0 20px"
        w={{ base: '100%', lg: '50%' }}
        h="80px"
        borderRadius="14px"
        bgColor="card_bg"
        bgImage={yield_bg}
        backgroundSize="100% 100%"
      >
        <Text fontSize="16px" color="primary_gray">
          Pending Yield
        </Text>
        <Skeleton isLoaded={!showYieldLoading}>
          {isApp ? (
            <HStack gap="20px">
              <VStack align="flex-end">
                <PendingYieldValue yieldList={yieldList} />
                <Button
                  isLoading={claimLoading}
                  disabled={Number(myPosYieldValue) <= 0 || claimLoading}
                  w="100px"
                  h="24px"
                  onClick={() => setIsOpenModal(true)}
                  borderRadius="8px"
                  fontSize="12px"
                  fontWeight="500"
                >
                  Claim All
                </Button>
              </VStack>

              {Number(myPosYieldValue) > 0 && <Icon xlinkHref="#icon-icon_spread" onClick={() => setIsOpenModal(true)} />}
            </HStack>
          ) : (
            <HStack onClick={() => setIsOpenModal(true)}>
              <PendingYieldValue yieldList={yieldList} />
              {Number(myPosYieldValue) > 0 && <Icon xlinkHref="#icon-icon_spread" />}
              <Button
                isLoading={claimLoading}
                disabled={Number(myPosYieldValue) <= 0 || claimLoading}
                w="112px"
                h="32px"
                borderRadius="8px"
                fontSize="14px"
                fontWeight="500"
              >
                Claim All
              </Button>
            </HStack>
          )}
        </Skeleton>
      </HStack>
      <PendingYieldModal
        isOpen={isOpenModal}
        claimLoading={claimLoading}
        onClose={() => setIsOpenModal(false)}
        changeClaimLoading={(status: boolean) => setClaimLoading(status)}
      />
    </Stack>
  )
}

export default LiquidityAndYield
